import {Component, OnInit, HostListener} from '@angular/core';

@Component({
  selector: 'app-game-engine',
  templateUrl: './game-engine.component.html',
  styleUrls: ['./game-engine.component.less']
})
export class GameEngineComponent implements OnInit {

  constructor() {

    this.shapes[0] = [
      [false, true, false, false],
      [false, true, false, false],
      [false, true, false, false],
      [false, true, false, false]
    ];
    this.shapes[1] = [
      [false, true, false, false],
      [false, true, true, false],
      [false, true, false, false],
      [false, false, false, false]
    ];
    this.shapes[2] = [
      [false, true, false, false],
      [false, true, false, false],
      [false, true, true, false],
      [false, false, false, false]
    ];
    this.shapes[3] = [
      [false, true, false, false],
      [false, true, true, false],
      [false, false, true, false],
      [false, false, false, false]
    ];
    this.shapes[4] = [
      [false, false, true, false],
      [false, true, true, false],
      [false, true, false, false],
      [false, false, false, false]
    ];
    this.shapes[5] = [
      [false, false, true, false],
      [false, false, true, false],
      [false, true, true, false],
      [false, false, false, false]
    ];
    this.shapes[6] = [
      [false, false, false, false],
      [false, true, true, false],
      [false, true, true, false],
      [false, false, false, false]
    ];

  }

  currentEngine: string = 'dom';
  size = {width: 10, height: 20};
  currentArea: boolean[][];
  shapePosition: { x: number, y: number } = {x: 3, y: -4};
  currentShape;
  nextShape;
  shapes: boolean[][][] = [];
  gameShowArea: boolean[][];
  timer: number;
  checkShape = {
    Right: () => {
      return this.currentShape.every((arr, i) => {
        return arr.every((value, j) => {
          if (this.currentShape[i][j] === true && i + this.shapePosition.x >= this.size.width) {
            return false;
          } else {
            return true;
          }
        });
      });
    },
    Left: () => {
      return this.currentShape.every((arr, i) => {
        return arr.every((value, j) => {
          if (this.currentShape[i][j] === true && i + this.shapePosition.x < 0) {
            return false;
          } else {
            return true;
          }
        });
      });
    },
    LR: () => {
      return this.checkShape.Left() && this.checkShape.Right();
    },
    Top: () => {
      return this.currentShape.every((arr, i) => {
        return arr.every((value, j) => {
          if (this.currentShape[i][j] === true && j + this.shapePosition.y < 0) {
            return false;
          } else {
            return true;
          }
        });
      });
    },
    Bottom: () => {
      return this.currentShape.every((arr, i) => {
        return arr.every((value, j) => {
          if (this.currentShape[i][j] === true && j + this.shapePosition.y >= this.size.height) {
            // console.log(i, j, value);
            return false;
          } else {
            return true;
          }
        });
      });
    },
    Block: () => {
      return this.currentShape.every((arr, i) => {
        return arr.every((value, j) => {
          if (value === false) {
            return true;
          } else if (!this.currentArea[this.shapePosition.x + i][this.shapePosition.y + j]) {
            return true;
          } else {
            // console.log(i, j, value);
            return false;
          }
        });
      });
    }
  };


  initArea(): boolean[][] {

    const area = new Array(this.size.width);
    for (let i = 0; i < this.size.width; ++i) {
      area[i] = new Array(this.size.height);
      for (let j = 0; j < this.size.height; ++j) {
        area[i][j] = false;
      }
    }
    return area;
  }

  goNextShape() {
    for (let i = 0; i < 4; ++i) {
      for (let j = 0; j < 4; ++j) {
        if (this.currentShape[i][j] === true) {
          this.currentArea[this.shapePosition.x + i][this.shapePosition.y + j] = true;
        }
      }
    }
    this.currentShape = this.nextShape;
    this.nextShape = this.genNextShape();
    this.shapePosition.x = 3;
    this.shapePosition.y = -4;
  }

  genNextShape(): boolean[][] {
    const i = Math.floor(Math.random() * this.shapes.length);
    return this.shapes[i];
  }

  initGame() {
    this.currentArea = this.initArea();
    this.gameShowArea = this.initArea();
    this.currentShape = this.genNextShape();
    this.nextShape = this.genNextShape();
  }

  genShowArea() {
    // this.gameShowArea = this.currentArea;
    for (let i = 0; i < this.size.width; ++i) {
      for (let j = 0; j < this.size.height; ++j) {
        this.gameShowArea[i][j] = this.currentArea[i][j];
      }
    }
    for (let i = 0; i < 4; ++i) {
      for (let j = 0; j < 4; ++j) {
        if (j + this.shapePosition.y >= 0 && this.currentShape[i][j] === true) {
          this.gameShowArea[i + this.shapePosition.x][j + this.shapePosition.y] = true;
        }
      }
    }

  }

  rotateShape() {
    const originShape = this.currentShape;
    const rotatedShape = [new Array(4), new Array(4), new Array(4), new Array(4)];
    for (let i = 0; i < 4; ++i) {
      for (let j = 0; j < 4; ++j) {
        rotatedShape[i][j] = this.currentShape[j][3 - i];
      }
    }
    this.currentShape = rotatedShape;
    if (!(this.checkShape.Block() && this.checkShape.LR())) {
      this.checkShape = originShape;
    }
  }


  eliminateRow() {
    let eliminatedRowCount = 0;
    for (let j = 0; j < this.size.height; ++j) {
      let flag = true;
      for (let i = 0; i < this.size.width; ++i) {
        if (!this.currentArea[i][j]) {
          flag = false;
          break;
        }
      }
      if (flag) {
        ++eliminatedRowCount;
        for (let k = j; k > 0; --k) {
          for (let i = 0; i < this.size.width; ++i) {
            this.currentArea[i][k] = this.currentArea[i][k - 1];
          }
        }
        for (let i = 0; i < this.size.width; ++i) {
          this.currentArea[i][0] = false;
        }
      }
    }
  }

  ngOnInit() {
    this.initGame();
    this.timer = window.setInterval(() => {
      ++this.shapePosition.y;

      if (!(this.checkShape.Block() && this.checkShape.Bottom())) {// 判断方块是否已到达底部
        --this.shapePosition.y;
        if (!this.checkShape.Top()) {// 判断方块是否已经溢出
          window.clearInterval(this.timer);
          alert('fail');
        }
        this.goNextShape();

        this.eliminateRow();
      }

      this.genShowArea();
    }, 400);
  }


  @HostListener('window:keydown', ['$event'])
  keydownHandler(event: KeyboardEvent) {
    // console.log(event);

    switch (event.which) {
      case 38:
        this.rotateShape();

        break;
      case 40:
        ++this.shapePosition.y;
        if (!(this.checkShape.Bottom() && this.checkShape.Block())) {
          --this.shapePosition.y;
        }
        break;
      case 37:
        --this.shapePosition.x;
        if (!(this.checkShape.Left() && this.checkShape.Block())) {
          ++this.shapePosition.x;
        }
        break;
      case 39:
        ++this.shapePosition.x;
        if (!(this.checkShape.Right() && this.checkShape.Block())) {
          --this.shapePosition.x;
        }
        break;
    }
  }
}





