import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';


import { AppComponent } from './app.component';
import { GameEngineComponent } from './game-engine/game-engine.component';
import { DomGraphEngineComponent } from './dom-graph-engine/dom-graph-engine.component';
import { CanvasGraphEngineComponent } from './canvas-graph-engine/canvas-graph-engine.component';
import { ThreejsGraphEngineComponent } from './threejs-graph-engine/threejs-graph-engine.component';


@NgModule({
  declarations: [
    AppComponent,
    GameEngineComponent,
    DomGraphEngineComponent,
    CanvasGraphEngineComponent,
    ThreejsGraphEngineComponent
  ],
  imports: [
    BrowserModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



